.articleListSmall {
  .articleListSmall__item {
    overflow: hidden;
    @include mobileBorder(); }
  .articleListSmall__image {
    float: left;
    margin-right: 20px; }

  .articleListSmall__headline {
    @include letterSpacing();
    font-size: 1.8rem;
    font-weight: 700;
    margin-top: 0px;
    margin-bottom: 10px;
    @include lg {
      margin-top: 0px;
      margin-left: 110px; }
    &:hover a {
      color: #658da2; } }
  .articleMetadata {
    @include lg {
      margin-left: 110px; } } }

.articleListSmall--latestNews {
  .articleListSmall__image {
    float: left;
    @include md {
      float: none; }
    @include lg {
      float: left;
      width: 90px; }
    img {
      width: 90px;
      @include md {
        width: 100%; }
      @include lg {
        width: auto; } } }
  .articleListSmall__headline {
    margin-top: 10px;
    @include lg {
      margin-top: 0px;
      margin-left: 110px; } } }
