.header {
  font-size: 0;
  margin: 0px 0px 20px 0px;
  @include sm {
    margin: 20px 0px 40px 0px; }
  .header__weather {
    font-family: $fontSansSerif;
    font-size: 1.4rem;
    margin: 10px 0px;
    display: flex;
    align-items: center;
    img {
      height: 25px;
      margin-right: 15px; } }
  .header__weather__info {
    margin: 0px 10px;
    br {
      display: none;
      @include sm {
        display: block; } } }
  .weather-temperature {
    font-size: 2.5em;
    font-weight: 200;
    @include sm {
      font-size: 3.5em; } }

  .weather-place {
    font-size: 1.15em; }
  .weather-description {
    font-size: 1.15em;
    text-transform: capitalize; }
  .header__logo {
    display: block;
    font-size: 1.8rem;
    text-align: center;
    margin: 10px 0px;
    @include sm {
      margin: 0px; }
    img {
      max-width: 400px; } }

  .header__search {
    font-family: $fontSerif;
    font-size: 3.6rem;
    text-align: right;
    font-style: italic;
    input {
      font-size: 3.6rem;
      max-width: 270px;
      text-align: right;
      padding-right: 10px;
      border: none;
      background: transparent;
      outline: none;
      @include sm {
        max-width: 400px; } }
    button {
      background: no-repeat center url('../img/search.svg');
      width: 27px;
      height: 27px;
      border: none;
      text-indent: -9999px;
      vertical-align: sub; }
    .gsc-input-box {
      border: none;
      height: auto; }
    .gsc-input-box-hover {
      border: none;
      box-shadow: none; }
    td.gsc-search-button {
      background: url('../img/search.svg') no-repeat center;
      width: 27px;
      height: 49px;
      display: block; }
    .cse input.gsc-search-button, input.gsc-search-button {
      width: 27px;
      height: 49px; }
    .cse input.gsc-search-button:hover, input.gsc-search-button:hover {
      background: none; }
    .gsc-input-box-focus {
      box-shadow: none; } } }
